module.exports = "Každé logo, ať už patří instituci či produktu, má definovanou bezpečnou zónu, která zabraňuje jeho slévání s ostatními grafickými prvky. Aby bylo zajištěno, že logo vždy vynikne, bez ohledu na velikost, je bezpečná zóna stanovena procentuálně. 80 % vertikální výšky loga odpovídá výšce pomyslného obdélníku či čtverce, který tvoří bezpečnou zónu, zatímco polovina ze zbývajících 40 % tvoří rozpětí bezpečné zóny okolo loga. Tento princip je ilustrován na příkladu loga coalfamily a aplikuje se univerzálně pro všechny členy rodiny.\n```image\nscale: true\nspan: 3\ntitle: \"Bezpečná zóna | Poměr (4:1)\"\nsrc: \"src/coalfamily/safezone/coalfamily_safezone_LONG.png\"\n```\n```image\nscale: true\nspan: 3\ntitle: \"Bezpečná zóna | Poměr (1:1)\"\nsrc: \"src/coalfamily/safezone/coalfamily_safezone_SQUARE.png\"\n```\n\n## Možnosti použití\nLogo je klíčovým prvkem celé vizuální identity značky. Pracujeme s ním v mnoha případech, na různých materiálech a prvcích. Je také symbolem, který reprezentuje značku a uchovává se v myslích zákazníků i široké veřejnosti. Proto je třeba logo využívat tak, aby nebylo znehodnocováno špatným umístěním, chybnou manipulací či nesprávným barevným variováním."

